* {
  margin: 0;
  padding: 0;
  font-family: "Times New Roman", Times, serif;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  min-width: 100%;
  background: #16384c;
  padding-top: 100px;
  padding-bottom: 100px;
}

.container {
  position: relative;
  /* width: 96vw; */
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  padding: 30px;
}
